* {
	margin: 0;
	padding: 0;
}
body {
	background: #000;
}
li {
	list-style: none;
}
a {
	text-decoration: none;
}

.row-center-between {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.loading {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	color: #fff;
	font-size: 32px;
	margin-top: 50px;
}